import React from 'react';
import ReactDOM from 'react-dom';
import './tailwind.css'
import 'bootstrap/dist/css/bootstrap.min.css'
import './index.css';
import './App.css'
import App from './App';
import {Provider} from "react-redux"
import store from "./redux"

ReactDOM.render(
  <React.StrictMode>
    {/* <Router> */}
      <Provider store={store}>
        <App />
      </Provider>
    {/* </Router> */}
  </React.StrictMode>,
  document.getElementById('root')
);

